import React, { Component } from 'react'
import CustomBreadcrumb from '@/components/CustomBreadcrumb'
import { Layout, Divider, Row, Col, Steps, Button, Form, Input, Select, Alert, Result } from 'antd'
import '@/style/view-style/form.scss'

const { Step } = Steps
const { Option } = Select

const formItemLayout = {
    labelCol: {
        span: 8
    },
    wrapperCol: {
        span: 8
    }
}

const tailFormItemLayout = {
    wrapperCol: {
        offset: 8
    }
}

class Step1 extends Component {
    handleSelectChange = value => {
        this.props.form.setFieldsValue({
            Email: `${value === 'kenan' ? 'kenan@google.com' : 'maoli@google.com'}`
        })
    }

    step1Submit = e => {
        e.preventDefault()
        this.props.form.validateFields((err, val) => {
            if (!err) {
                this.props.getFormData(val)
                this.props.setCurrent(1)
            }
        })
    }

    render() {
        const { getFieldDecorator } = this.props.form
        const selectBefore = getFieldDecorator('Type', {
            initialValue: 'twitter'
        })(
            <Select style={{ width: '8rem' }}>
                <Option value='twitter'>twitter</Option>
                <Option value='facebook'>facebook</Option>
                <Option value='weixin'></Option>
            </Select>
        )
        return (
            <div>
                <Form hideRequiredMark {...formItemLayout}>
                    <Form.Item label=''>
                        {getFieldDecorator('User', {
                            initialValue: '',
                            rules: [
                                {
                                    required: true,
                                    message: ''
                                }
                            ]
                        })(
                            <Select onChange={this.handleSelectChange}>
                                <Option value=''></Option>
                                <Option value=''></Option>
                            </Select>
                        )}
                    </Form.Item>
                    <Form.Item label=''>
                        {getFieldDecorator('Email', {
                            initialValue: 'kenan@google.com',
                            rules: [
                                {
                                    required: true,
                                    message: ''
                                }
                            ]
                        })(
                            <Select disabled>
                                <Option value='kenan@google.com'>kenan@google.com</Option>
                                <Option value='maoli@google.com'>maoli@google.com</Option>
                            </Select>
                        )}
                    </Form.Item>
                    <Form.Item label=''>
                        {getFieldDecorator('Password', {
                            initialValue: '!',
                            rules: [
                                {
                                    required: true,
                                    message: ''
                                }
                            ]
                        })(<Input placeholder='' />)}
                    </Form.Item>
                    <Form.Item label=''>
                        {getFieldDecorator('Code', {
                            initialValue: 'kenan0528',
                            rules: [
                                {
                                    required: true,
                                    message: ''
                                }
                            ]
                        })(<Input addonBefore={selectBefore} placeholder='' />)}
                    </Form.Item>
                    <Form.Item {...tailFormItemLayout}>
                        <Button type='primary' onClick={this.step1Submit}>
                            
                        </Button>
                    </Form.Item>
                </Form>
            </div>
        )
    }
}

class Step2From extends Component {
    state = {
        visible: true,
        iconLoading: false
    }

    handleClose = () => {
        this.setState({ visible: false })
    }

    step2Submit = () => {
        this.setState({ iconLoading: true })
        setTimeout(() => {
            this.setState({ iconLoading: false })
            this.props.setCurrent(2)
        }, 2000)
    }
    step2Cancle = () => {
        this.props.setCurrent(0)
    }
    render() {
        const { formData } = this.props
        return (
            <div>
                <Row>
                    <Col span={8} offset={8}>
                        {this.state.visible ? (
                            <Alert
                                message='!'
                                type='warning'
                                closable
                                banner
                                afterClose={this.handleClose}
                                {...formItemLayout}
                            />
                        ) : null}
                    </Col>
                </Row>
                <Form hideRequiredMark {...formItemLayout} className='show-data'>
                    <Form.Item label=''>{formData.User}</Form.Item>
                    <Form.Item label=''>{formData.Email}</Form.Item>
                    <Form.Item label=''>{formData.Password}</Form.Item>
                    <Form.Item label=''>{formData.Type}</Form.Item>
                    <Form.Item label=''>{formData.Code}</Form.Item>
                    <Divider />
                    <Form.Item {...tailFormItemLayout}>
                        <Button type='primary' loading={this.state.iconLoading} onClick={this.step2Submit}>
                            
                        </Button>
                        <Button onClick={this.step2Cancle}></Button>
                    </Form.Item>
                </Form>
            </div>
        )
    }
}

class Step3From extends Component {
    oneMore = () => {
        this.props.setCurrent(0)
    }
    render() {
        return (
            <Result
                status='success'
                title='!'
                subTitle='!'
                extra={[
                    <Button type='primary' key='console' onClick={this.oneMore}>
                        
                    </Button>,
                    <Button key='buy'></Button>
                ]}
            />
        )
    }
}

const Step1From = Form.create()(Step1)

class FormStepView extends Component {
    state = {
        current: 0,
        formData: null
    }

    getFormData = val => {
        this.setState({
            formData: val
        })
    }

    setCurrent = val => {
        this.setState({
            current: val
        })
    }

    render() {
        const { current, formData } = this.state
        return (
            <Layout className='animated fadeIn'>
                <div>
                    <CustomBreadcrumb arr={['', '']}></CustomBreadcrumb>
                </div>
                <div className='base-style'>
                    <h3></h3>
                    <Divider />
                    <p></p>
                </div>
                <Row>
                    <Col>
                        <div className='base-style'>
                            <Divider orientation='left'></Divider>
                            <div>
                                <Steps style={{ margin: '3rem auto', maxWidth: '65rem' }} current={current}>
                                    <Step title=''></Step>
                                    <Step title=''></Step>
                                    <Step title=''></Step>
                                </Steps>

                                {current === 0 && (
                                    <Step1From getFormData={this.getFormData} setCurrent={this.setCurrent} />
                                )}
                                {current === 1 && <Step2From formData={formData} setCurrent={this.setCurrent} />}
                                {current === 2 && <Step3From setCurrent={this.setCurrent} />}
                            </div>
                        </div>
                    </Col>
                </Row>
            </Layout>
        )
    }
}

export default FormStepView
